.contacts {
  background-color: rgb(255, 255, 255);
}

.contacts__container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 38px 30px;
}

.contacts .section__title {
  padding-bottom: 25px;

  font-size: 18px;
}

.contacts__map {
  width: 100%;
  height: 457px;

  background-image: url("../img/map-mobile@1x.jpg");
  background-color: rgb(245, 245, 245);

  @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/map-mobile@2x.jpg");
    background-size: 322px auto;
  }
}

.contacts__map iframe {
  width: 100%;
  height: 100%;
}

.contacts__row:first-child {
  margin-bottom: 30px;
}

.contacts__field {
  padding-right: 30px;
}

.contacts__field,
.contacts__value {
  display: table-cell;

  line-height: 30px;
}

.contacts__value--mail {
  color: inherit;
  text-decoration: none;

  border-bottom: #46c1ae 1px solid;
  cursor: pointer;

  &:hover,
  &:focus {
    color: #46c1ae;
  }

  &:active {
    opacity: 0.3;
  }
}

.contacts__button {
  display: block;

  text-align: center;
}

@media (min-width: $tablet-width) {
  .contacts {
    width: $tablet-width;
    margin: 0 auto;
    margin-bottom: 15px;
  }

  .contacts__container {
    position: relative;

    margin: 0;
    padding: 0;
  }

  .contacts__container:first-child::before {
    content: "";
    position: absolute;
    top: -15px;
    right: 50px;

    display: block;
    width: 47px;
    height: 81px;

    background-image: url("../img/icon-flag.svg");
    background-repeat: no-repeat;
  }

  .contacts .section__title {
    padding-left: 50px;

    font-size: 43px;
    line-height: 50px;
  }

  .contacts__table {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
    padding: 25px 50px;

    font-size: 17px;
    line-height: 30px;
  }

  .contacts__field {
    padding-right: 25px;
  }

  .contacts__row {
    width: 50%;
  }

  .contacts__map {
    background-image: url("../img/map-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/map-tablet@2x.jpg");
      background-size: 768px auto;
    }
  }

  .contacts__button {
    position: relative;
    left: 50%;

    display: inline-block;
    width: auto;
    margin: 55px 0 0 0;
    padding: 14px 40px;

    transform: translateX(-50%);
  }
}


@media (min-width: $desktop-width) {
  .contacts {
    position: relative;

    display: flex;
    flex-wrap: wrap;
    width: $desktop-width - 100px;
    margin-bottom: 10px;
    padding: 95px 0 0 0;
  }

  .contacts::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;

    height: 23px;

    background-image: url("../img/bg-zigzag-line.svg");
    background-repeat: repeat-x;
    background-position: bottom;
  }

  .contacts__container {
    width: 50%;
    padding-left: 175px;
  }

  .contacts__container .contacts__title {
    padding: 70px 0 50px 0;

    font-size: 53px;
    line-height: 57px;
  }

  .contacts__container:first-child::before {
    top: 65px;
    left: 65px;
  }

  .contacts__table {
    flex-direction: column;
    margin: 0;
    margin-bottom: 60px;
    padding: 0;
    padding-bottom: 75px;
  }

  .contacts__row {
    width: 100%;

    &:first-of-type {
      padding-bottom: 0;
    }
  }

  .contacts__field {
    min-width: 75px;
    padding-right: 20px;
  }

  .contacts__value {
    min-width: 200px;
  }

  .contacts__map {
    width: 50%;
    height: auto;

    background-image: url("../img/map-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/map-desktop@2x.jpg");
      background-size: 525px auto;
    }
  }

  .contacts__container:last-of-type {
    position: relative;
    top: -60px;

    transform: none;
  }

  .contacts__container:last-of-type .contacts__button {
    position: static;

    margin: 0;

    transform: none;
  }
}
